@import url('https://fonts.googleapis.com/css?family=Audiowide');

* {
    margin: 0;
    padding: 0;
}


body {
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: scroll;
    background-color: #0d1019;
    font-family: "Audiowide";
}







.data-grid-contain{
    height:82.5vh;
    width:90vw;
    margin-left: auto;
    margin-right: auto;
    padding-top: 10px;
    
}

.bottom-bar{
    position : relative;
    height : 6.1vh;
    
}

.footer{
    position : absolute;
    bottom : 0;
}

